<script lang="ts">
    import { Button, ButtonDropdown, CardBody, Card, DropdownItem, DropdownMenu, DropdownToggle, Input } from "sveltestrap";
    import type { SectionType } from "./types/SectionType";
    import Section from "./components/Section.svelte";
    import type { AppState } from "./types/AppState";
    import { Utils } from "./types/Utils";

    export let appState: AppState = {
        chapter: 0,
        section: "",
        title: "",
        maxQuestions: 0,
        showQuestions: false,
        customSections: null,
    };

    interface ChapterList {
        [key: string]: SectionType[];
    }

    const chapters: ChapterList = {
        "2": [
            {
                chapter: 2,
                name: "Polyatomic Ions",
                questions: 10,
            },
            {
                chapter: 2,
                name: "Chemical Nomenclature",
                questions: 8,
            },
        ],
        "4": [
            {
                chapter: 4,
                name: "Balancing Equations",
            },
            {
                chapter: 4,
                name: "Oxidation Numbers",
            },
            {
                chapter: 4,
                name: "Classifying Reactions",
            },
            {
                chapter: 4,
                name: "Reaction Stoichiometry",
            },
            {
                chapter: 4,
                name: "Acid-Base Titration",
            },
            {
                chapter: 4,
                name: "Exam",
                questions: 15,
            },
            {
                chapter: 4,
                name: "Multiple Choice",
                questions: 4,
                max: 4,
            },
        ],
        "9 & 5": [
            {
                chapter: 9,
                name: "Unit Conversions",
            },
            {
                chapter: 9,
                name: "Ideal Gas Law",
            },
            {
                chapter: 9,
                name: "Combined Gas Law",
            },
            {
                chapter: 9,
                name: "Gas Stoichiometry",
            },
            {
                chapter: 9,
                name: "Multiple Choice",
                questions: 3,
                max: 3,
            },
            {
                chapter: 5,
                name: "Energy Basics",
            },
            {
                chapter: 5,
                name: "Heat Transfer Equation",
            },
            {
                chapter: 5,
                name: "Calorimetry",
            },
            {
                chapter: 5,
                name: "Enthalpy",
                questions: 4
            },
            {
                chapter: 5,
                name: "Exam",
                questions: 15,
            },
            {
                chapter: 5,
                name: "Multiple Choice",
                questions: 1,
                max: 1,
            },
        ],
    };

    function showQuiz(chapter: number, section: string, questions: number = 5, title: string = null) {
        title ??= `Quiz: Chapter ${chapter} - ${section}`;
        appState.customSections = null;
        appState.showQuestions = true;
        appState.chapter = chapter;
        appState.section = section;
        appState.title = title;
        appState.maxQuestions = questions;
    }

    let customSections: SectionType[] = [];

    function addSection(section: SectionType) {
        section.customQuestions ??= 1;
        customSections.push(section);
        customSections = customSections;
    }

    function removeSection(section: SectionType) {
        let index = customSections.findIndex((s) => s.chapter == section.chapter && s.name == section.name);
        delete customSections[index].customQuestions;
        customSections.splice(index, 1);
        customSections = customSections;
    }

    function changeSectionQuestions(section: SectionType, index: number) {
        if (customSections[index].customQuestions < 1) {
            customSections[index].customQuestions = 1;
        } else if (section.max && customSections[index].customQuestions > section.max) {
            customSections[index].customQuestions = section.max;
        } else if (customSections[index].customQuestions > 20) {
            customSections[index].customQuestions = 20;
        }
    }

    function startCustomQuiz() {
        appState.showQuestions = true;
        appState.title = "Custom Quiz";
        appState.maxQuestions = 0;
        customSections.forEach((s) => (appState.maxQuestions += s.customQuestions));
        appState.customSections = customSections;
    }
</script>

{#each Object.entries(chapters) as chapter}
    <Section title="Chemistry - Chapter{chapter[0].length >= 3 ? 's' : ''} {chapter[0]}">
        {#each chapter[1] as section}
            {#if section.name == "Exam"}
                <Button
                    color="primary"
                    on:click={() => showQuiz(section.chapter, "*", section.questions, `Practice Exam: Chapter${chapter[0].length >= 3 ? "s" : ""} ${chapter[0]}`)}
                    >Practice Exam</Button
                >
            {:else if section.name != "Multiple Choice"}
                <Button on:click={() => showQuiz(section.chapter, section.name, section.questions)}>{section.name}</Button>
            {/if}
        {/each}
    </Section>
{/each}

<Section title="Custom Quiz">
    {#each customSections as section, index}
        <Card style="margin-block: 0.25rem; padding: 0.15rem;">
            <CardBody style="padding: 0.325rem; padding-block: 0.2rem; display: flex;"
                ><p style="margin-block: auto; margin-left: 0.25rem; font-size: 1.1rem;">Ch. {section.chapter} - {section.name}</p>
                <Button on:click={() => removeSection(section)} color="danger" style="margin-left: 0.6rem; max-height: 2.1rem; align-items: center; display: flex;">Remove</Button>
                <span style="flex: 1" />
                <p style="color: gray; margin-block: auto; font-style: italic; margin-right: 0.5rem;"># of questions</p>
                <Input type="number" style="max-width: 5rem;" bind:value={customSections[index].customQuestions} on:change={() => changeSectionQuestions(section, index)} />
            </CardBody>
        </Card>
    {:else}
        <h5 style="color: gray">No Sections Added</h5>
    {/each}
    <hr />
    <span style="display: flex; gap: 0.35rem; align-items: center;">
        <ButtonDropdown>
            <DropdownToggle caret>Add</DropdownToggle>
            <DropdownMenu>
                <DropdownItem header>Ch. {Object.keys(chapters)[0]}</DropdownItem>
                {@const sections = Utils.flattenArray(Object.values(chapters)).filter((s) => s.name != "Exam")}
                {#each sections as section, index}
                    {#if index != 0 && sections[index - 1].chapter != section.chapter}
                        <DropdownItem divider />
                        <DropdownItem header>Ch. {section.chapter}</DropdownItem>
                    {/if}
                    <DropdownItem disabled={customSections.includes(section)} on:click={() => addSection(section)}>
                        {section.name}
                    </DropdownItem>
                {/each}
            </DropdownMenu>
        </ButtonDropdown>
        <Button color="primary" on:click={startCustomQuiz} disabled={customSections.length == 0}>Start Custom Quiz</Button>
    </span>
</Section>
