<script lang="ts">
    import { Input } from "sveltestrap";
    import { AdvancedQuestionAnswer, Question, QuestionResult, QuestionType } from "../types/Question";
    import SameLine from "./SameLine.svelte";
    import { Utils } from "../types/Utils";

    let answerData: string = "";
    let editable: boolean = true;
    let radioGroup;
    let answersData: string[] = [];
    let checkboxData: boolean[] = [];
    let checkboxValues: string[] = [];
    let advancedAnswers: AdvancedQuestionAnswer = { values: [], match: null };
    let currentQuestion: Question;
    export let message: string = "";

    export function checkQuestion(): boolean | number {
        editable = false;
        let result: QuestionResult;

        if (currentQuestion.type == QuestionType.Advanced) {
            delete advancedAnswers.values;
            advancedAnswers.values = Object.values(advancedAnswers);
            advancedAnswers.values ??= [];
            advancedAnswers.match = (ids, correctAnswers) =>
                Utils.arraysEqualStrict(
                    ids.map((id) => advancedAnswers[id]),
                    correctAnswers
                );
            result = currentQuestion.check(advancedAnswers);
        } else {
            result = currentQuestion.check(
                currentQuestion.type == QuestionType.MultipleChoice
                    ? radioGroup
                    : currentQuestion.type == QuestionType.MultiTextInput
                    ? answersData
                    : currentQuestion.type == QuestionType.MultiSelect
                    ? checkboxData.map((a, index) => (a == true ? checkboxValues[index] : "")).filter((a) => a != "")
                    : answerData
            );
        }
        if ((typeof result.correct == "number" && result.correct < 1) || (typeof result.correct == "boolean" && !result.correct)) {
            message = result.msgFail ?? "";
        } else {
            message = "";
        }
        return result.correct;
    }

    export function setQuestion(question: Question) {
        clear();
        currentQuestion = question;
        editable = true;
    }

    function clear() {
        answerData = "";
        radioGroup = null;
        answersData = [];
        checkboxData = [];
        advancedAnswers = { values: [], match: null };
    }
</script>

{#if currentQuestion}
    <p>{@html currentQuestion.text}</p>
    {#if currentQuestion.type === QuestionType.TextInput}
        <SameLine>
            {#if currentQuestion.data?.prefix}
                <p style="margin-block: auto;">{@html currentQuestion.data.prefix}</p>
            {/if}
            <Input bind:value={answerData} disabled={!editable} style="width: {currentQuestion.data?.size ?? 13}rem" placeholder="Answer" />
            {#if currentQuestion.data?.suffix}
                <p style="margin-block: auto;">{@html currentQuestion.data.suffix}</p>
            {/if}
        </SameLine>
    {:else if currentQuestion.type === QuestionType.MultipleChoice}
        {#each currentQuestion.data as answer}
            <Input type="radio" bind:group={radioGroup} value={answer} label={answer} disabled={!editable} />
        {/each}
    {:else if currentQuestion.type == QuestionType.MultiTextInput}
        {#each currentQuestion.data as field, index}
            <SameLine>
                <p style="margin-block: auto;">{@html field}</p>
                <Input bind:value={answersData[index]} disabled={!editable} style="width: 13rem" />
            </SameLine>
        {/each}
    {:else if currentQuestion.type == QuestionType.MultiSelect}
        {#each currentQuestion.data as answer, index}
            {Utils.void((checkboxValues[index] = answer))}
            <Input type="checkbox" bind:checked={checkboxData[index]} label={answer} disabled={!editable} />
        {/each}
    {:else if currentQuestion.type == QuestionType.Advanced}
        {#each currentQuestion.data as directive, index}
            {#if directive.type == "text"}
                <br />
                <p>{directive.data}</p>
            {:else if directive.type == "multi_choice"}
                {#each directive.data.answers as answer}
                    <Input type="radio" bind:group={advancedAnswers[directive.id]} value={answer} label={answer} disabled={!editable} />
                {/each}
            {:else if directive.type == "multi_select"}
                {#each directive.data.answers as answer, index}
                    {Utils.void((advancedAnswers[directive.id][index] = answer))}
                    <Input type="checkbox" bind:checked={advancedAnswers[directive.id][index]} label={answer} disabled={!editable} />
                {/each}
            {:else if directive.type == "dropdown"}
                <SameLine gap={2}>
                    {#if directive?.data?.prefix}
                        <p style="margin-block: auto;">{@html directive.data.prefix}</p>
                    {/if}
                    <Input type="select" bind:value={advancedAnswers[directive.id]} style="width: max-content">
                        {#each directive.data.answers as answer}
                            <option>{answer}</option>
                        {/each}
                    </Input>
                    {#if directive?.data?.suffix}
                        <p style="margin-block: auto;">{@html directive.data.suffix}</p>
                    {/if}
                </SameLine>
            {:else if directive.type == "text_input"}
                <SameLine>
                    {#if directive?.data?.prefix}
                        <p style="margin-block: auto;">{@html directive.data.prefix}</p>
                    {/if}
                    <Input bind:value={advancedAnswers[directive.id]} disabled={!editable} style="width: {currentQuestion.data?.size ?? 8}rem" />
                    {#if directive?.data?.suffix}
                        <p style="margin-block: auto;">{@html directive.data.suffix}</p>
                    {/if}
                </SameLine>
            {/if}
        {/each}
    {/if}
{/if}

<style global>
    :is(.form-check-label) {
        position: relative;
        bottom: 3px;
    }
</style>
