import BigNumber from "bignumber.js";
export class Utils {
    static getHashCode(str, seed = 0) {
        let h1 = 0xdeadbeef ^ seed, h2 = 0x41c6ce57 ^ seed;
        for (let i = 0, ch; i < str.length; i++) {
            ch = str.charCodeAt(i);
            h1 = Math.imul(h1 ^ ch, 2654435761);
            h2 = Math.imul(h2 ^ ch, 1597334677);
        }
        h1 = Math.imul(h1 ^ (h1 >>> 16), 2246822507) ^ Math.imul(h2 ^ (h2 >>> 13), 3266489909);
        h2 = Math.imul(h2 ^ (h2 >>> 16), 2246822507) ^ Math.imul(h1 ^ (h1 >>> 13), 3266489909);
        return 4294967296 * (2097151 & h2) + (h1 >>> 0);
    }
    static getRandomElement(array) {
        if (array == null || array.length == 0) {
            console.error("Empty array passed to getRandomElement<T>(T[]).");
            return null;
        }
        return array[Math.floor(Math.random() * array.length)];
    }
    static getRandomElements(array, elements) {
        if (elements > array.length) {
            console.error("Invalid number of elements passed to getRandomElements<T>(T[], number).");
            return null;
        }
        let elementsArray = [];
        for (let i = 0; i < elements; i++) {
            let element = Utils.getRandomElement(array);
            while (elementsArray.includes(element)) {
                element = Utils.getRandomElement(array);
            }
            elementsArray.push(element);
        }
        return elementsArray;
    }
    static void(action) {
        return "";
    }
    static randomChance(chance) {
        return Math.random() <= chance;
    }
    static randomNumber(min, max) {
        return Math.random() * (max - min) + min;
    }
    static randomNumber2(min1, max1, min2, max2) {
        return Utils.getRandomElement([this.randomNumber(min1, max1), this.randomNumber(min2, max2)]);
    }
    static randomInt(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
    }
    static round(value, places = 0) {
        return Math.round(value * Math.pow(10, places)) / Math.pow(10, places);
    }
    static arraysEqual(left, right) {
        return left.length == right.length && left.every((value, index) => value == right[index]);
    }
    static arraysEqualStrict(left, right) {
        return left.length == right.length && left.every((value, index) => Utils.removeUnderline(value) === Utils.removeUnderline(right[index]));
    }
    static arraysEqualUnordered(left, right) {
        left = left.sort();
        right = right.sort();
        return left.length == right.length && left.every((value, index) => value == right[index]);
    }
    static arraysEqualUnorderedCount(left, right) {
        left = left.sort();
        right = right.sort();
        return left.filter((value, index) => value == right[index]).length;
    }
    static isUpperCase(c) {
        return c != " " && c.toUpperCase() == c;
    }
    static isLowerCase(c) {
        return c != " " && c.toLowerCase() == c;
    }
    static getSignificantFigures(value) {
        if (value === 0) {
            return 0;
        }
        var t1 = ("" + Math.abs(value));
        var t2 = t1.replace(".", "");
        var i = t2.indexOf("e");
        if (i > -1) {
            return i;
        }
        if (t2.length < t1.length) {
            return t2.replace(/^0+/, '').length;
        }
        return t2.replace(/0+$/, '').length;
    }
    static toPrecisionBig(value, precision, latex = false) {
        let pre = value.toPrecision(precision);
        let val = pre.includes("e") ? new BigNumber(pre).toString() : pre;
        if (!val.includes('.') && val[val.length - 1] === "0" && val.replace("-", "")[precision - 1] === "0") {
            val = val.replace(RegExp(`(?<=-?[^-]{${precision - 1}})(.)`), latex ? "\\underline{$1}" : "<u>$1</u>");
        }
        return val;
    }
    static toPrecision(value, precision, latex = false) {
        let pre = new BigNumber(value).toPrecision(precision);
        let val = pre.includes("e") ? new BigNumber(pre).toString() : pre;
        if (!val.includes('.') && val[val.length - 1] === "0" && val.replace("-", "")[precision - 1] === "0") {
            val = val.replace(RegExp(`(?<=-?[^-]{${precision - 1}})(.)`), latex ? "\\underline{$1}" : "<u>$1</u>");
        }
        return val;
    }
    static toPrecisionBigNumber(value, precision) {
        return new BigNumber(new BigNumber(value).toPrecision(precision));
    }
    static toPrecisionBigNumberBig(value, precision) {
        return new BigNumber(value.toPrecision(precision));
    }
    static toPrecisionNumber(value, precision) {
        return Number(new BigNumber(value).toPrecision(precision));
    }
    static toBigNumber(value) {
        return new BigNumber(typeof (value) == "string" ? Utils.removeUnderline(value) : value);
    }
    static removeUnderline(value) {
        if (!value) {
            return value;
        }
        return value.replaceAll("</u>", "").replaceAll("<u>", "").replaceAll(/\\underline\{0\}/g, "0");
    }
    static shuffleArray(array) {
        let currentIndex = array.length, randomIndex;
        while (currentIndex > 0) {
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex--;
            [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
        }
        return array;
    }
    static equal(a, b) {
        return a.replace("</u>", "").replace("<u>", "") === b.replace("</u>", "").replace("<u>", "");
    }
    static log(value) {
        console.log(value);
        return value;
    }
    static wrapIgnore(str) {
        return "&#8203;" + str + "&#8203;";
    }
    static padLeft(str, length, padChar = " ") {
        return length <= str.length ? str : padChar.repeat(length - str.length) + str;
    }
    static decimalPlaces(numStr) {
        if (!numStr || !numStr.includes('.')) {
            return 0;
        }
        return Utils.removeUnderline(numStr).split('.')[1].length;
    }
    static toFixed(num, places) {
        return num.toFixed(places);
    }
    static toFixedBig(num, places) {
        return num.toFixed(places);
    }
    static greatestCommonDenominator(a, b) {
        [a, b] = [Math.abs(a), Math.abs(b)];
        if (b > a) {
            [a, b] = [b, a];
        }
        while (true) {
            if (b == 0)
                return a;
            a %= b;
            if (a == 0)
                return b;
            b %= a;
        }
    }
    static flattenArray(array) {
        return Array.prototype.concat(...array);
    }
}
