<script lang="ts">
    import { Alert, Button } from "sveltestrap";
    import Section from "./components/Section.svelte";
    import QuestionItem from "./components/QuestionItem.svelte";
    import { Utils } from "./types/Utils";
    import type { Question } from "./main";
    import { onMount } from "svelte";
    import { Questions } from "./types/Questions";
    import * as DateFns from "date-fns";
    import type { AppState } from "./types/AppState";
    import { QuestionType } from "./types/Question";

    export let appState: AppState = {
        chapter: 0,
        section: "",
        title: "",
        maxQuestions: 0,
        showQuestions: false,
        customSections: null,
    };

    let questionNumber: number = 1;
    let questionsCorrect: number = 0;
    let answerCorrect: boolean | "partial" | null = null;
    let questionItem: QuestionItem;
    let finished: boolean = false;
    let message: string = "";
    let currentQuestions: Question[] = [];
    let startTime: Date;
    $: currentQuestion = currentQuestions[questionNumber - 1];
    const questionRegex =
        /-?\d+\.?\d* (\w||\&deg;|\/)+|(increases|decreases|rises|falls|releases|absorbs|increase|decrease|release|absorb|increased|decreased)|\&\#8203;.+?\&\#8203;/g;

    onMount(() => {
        startTime = new Date();
        if (appState.customSections != null) {
            currentQuestions = [];
            appState.customSections.forEach((section) => {
                let questionsAdded = 0;
                while (questionsAdded < section.customQuestions) {
                    let question: Question = null;
                    let loops = 0;
                    while (
                        question == null ||
                        currentQuestions.some(
                            (q) =>
                                Utils.removeUnderline(q.text).replace(questionRegex, "").replaceAll(" ", "") ==
                                Utils.removeUnderline(question.text).replace(questionRegex, "").replaceAll(" ", "")
                        )
                    ) {
                        question = Questions.getQuestion(section.chapter, section.name);
                        loops++;
                        if (loops > 12) {
                            break;
                        }
                    }
                    currentQuestions.push(question);
                    questionsAdded++;
                }
            });
            currentQuestions = Utils.shuffleArray(currentQuestions);
        } else if (appState.section == "*") {
            currentQuestions = Questions.getExamQuestions(appState.chapter);
        } else {
            currentQuestions = [];
            while (currentQuestions.length < appState.maxQuestions) {
                let question: Question = null;
                let loops = 0;
                while (
                    question == null ||
                    currentQuestions.some(
                        (q) =>
                            Utils.removeUnderline(q.text).replace(questionRegex, "").replaceAll(" ", "") ==
                            Utils.removeUnderline(question.text).replace(questionRegex, "").replaceAll(" ", "")
                    )
                ) {
                    question = Questions.getQuestion(appState.chapter, appState.section);
                    loops++;
                    if (loops > 12) {
                        break;
                    }
                }
                currentQuestions.push(question);
            }
        }

        questionItem.setQuestion(currentQuestions[questionNumber - 1]);
    });

    function askExit() {
        if (!confirm("Are you sure you want to exit? Progress will not be saved.")) {
            return;
        }

        exit();
    }

    function exit() {
        questionNumber = 1;
        appState.showQuestions = false;
    }

    function submit() {
        let correct = questionItem.checkQuestion();
        if (typeof correct == "boolean") {
            answerCorrect = correct;
            if (answerCorrect) {
                questionsCorrect++;
            }
        } else if (typeof correct == "number") {
            if (correct <= 0) {
                answerCorrect = false;
            } else if (correct >= 1) {
                answerCorrect = true;
                questionsCorrect++;
            } else {
                answerCorrect = "partial";
                questionsCorrect += correct;
            }
        }
    }

    function next() {
        answerCorrect = null;

        if (questionNumber == appState.maxQuestions) {
            finished = true;
            return;
        }

        questionNumber++;
        questionItem.setQuestion(currentQuestions[questionNumber - 1]);
    }
</script>

{#if appState.showQuestions}
    <Section title={appState.title}>
        {#if !finished}
            <h4>Question {questionNumber}/{appState.maxQuestions}</h4>
            <hr />
            <QuestionItem bind:this={questionItem} bind:message />
            <hr />
            {#if answerCorrect === true}
                <Alert fade={false} color="success">Correct! {@html message ?? ""}</Alert>
            {:else if answerCorrect === false}
                <Alert fade={false} color="danger">Incorrect. {@html message ?? ""}</Alert>
            {:else if answerCorrect === "partial"}
                <Alert fade={false} color="warning">Partially correct. {@html message ?? ""}</Alert>
            {/if}
            {#if answerCorrect === null}
                <Button color="success" on:click={submit}>Submit</Button>
            {:else}
                <Button color="primary" on:click={next}>{questionNumber == appState.maxQuestions ? "Finish" : "Next"}</Button>
            {/if}
            <Button color="danger" on:click={askExit}>Exit</Button>
        {:else}
            {@const grade = Utils.round((questionsCorrect / appState.maxQuestions) * 100)}
            <h4>{appState.title.includes("Practice Exam") ? "Practice Exam" : "Quiz"} Completed</h4>
            <hr />
            {@const duration = DateFns.intervalToDuration({ start: startTime, end: new Date() })}
            <h5>
                Time Taken: {Utils.padLeft(duration.hours.toString(), 2, "0")}:{Utils.padLeft(duration.minutes.toString(), 2, "0")}:{Utils.padLeft(
                    duration.seconds.toString(),
                    2,
                    "0"
                )}
            </h5>
            <h5>
                Grade: <span style={"color: " + (grade == 100 ? "darkgreen" : grade >= 80 ? "green" : grade >= 60 ? "yellow" : "red")}>{grade}%</span>
            </h5>
            <hr />
            <Button color="success" on:click={exit}>End {appState.title.includes("Practice Exam") ? "Practice Exam" : "Quiz"}</Button>
        {/if}
    </Section>

    {#if !finished && currentQuestion && currentQuestion.type == QuestionType.TextInput && currentQuestion?.data?.suffix}
        <Alert color="secondary" fade={false} style="max-width: 60rem; margin-inline: auto;">Note: For math problems, make sure to only round to significant figures at the end. Rounding intermediate steps can introduce rounding error, which can cause your answer to be marked as incorrect.</Alert>
    {/if}
{/if}
